nav{
    background: rgba(0, 0, 0, 0.3);
    font-size: 2rem;
    width: max-content;
    display: block;
    padding: 0.8rem 1.5rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 0.8rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 1rem;
    border-radius: 50%;
    display: flex;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}


/* ================Media Queries (sm)=============== */
  
@media screen and (max-width: 600px) {
    nav{
        font-size: 2rem;
        padding: 0.5rem 0.5rem;
    }
}